<template>
  <div class="card-row">
    <div class="card-row_col">
      <div style="width:30px;text-align:center;line-height:40px">{{index}}</div>
    </div>
    <div class="card-row_col">
      <el-tooltip placement="left">
        <div slot="content">
          <div style="height:30px:width:30px">
            <img :src="item.content.image" style="height:100%:width:100%" />
          </div>
        </div>

        <div class="thumbnail">
          <img :src="item.content.image" />
        </div>
      </el-tooltip>
    </div>
    <div class="card-row_col">
      <div class="name">
        <div class="name_zh">{{ item["zhName"] }}</div>
        <div class="name_en">{{ item.name }}</div>
      </div>
    </div>
    <div class="card-row_col">
      <div style="width:30px;text-align:center;line-height:40px">{{item.variations.length}}</div>
    </div>
    <div class="card-row_col">
      <ul class="variations" v-if="item.variations.length>0">
        <li v-for="(obj, index) in item.variations" :key="index">
          <!-- {{obj.tid}} -->
          <!-- v-show="(item.variations.length>1&&obj.content.filename.slice(-1)=='0')||item.variations.length==1" -->

          <div
            class="variation"
            :class="{'variation--isOwnd':bookFile[obj.tid]}"
            @click="bookFile[obj.tid]=!bookFile[obj.tid]"
          >
            <div class="variation__thumbnail">
              <img :src="obj.content.image" />
            </div>

            <div class="variation__text">{{obj.content.bodyColor_zh}}</div>
          </div>
          <!-- {{obj.name}} -->
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "CardRow",
  props: {
    item: {
      type: Object,
      default: () => ({})
    },
    index: {
      type: Number,
      default: 0
    }
  },
  computed: {
    bookFile: {
      get() {
        return this.$store.state.bookFile;
      },
      set(value) {
        this.$store.commit("UPDATE_BOOKFILE", value);
      }
    }
  },
  data() {
    return {};
  },

  mounted() {},
  methods: {}
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.card-row {
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.04);
  background-color: #fff;
  // padding: 5px;
  border-radius: 3px;
  margin: 0 5px;
  border: solid 1px #eee;
  color: #2d3748;
  height: 40px;

  .card-row_col {
    vertical-align: top;
    height: 100%;
    display: inline-block;
    border: solid 1px #b1bcce;
  }

  .name {
    padding: 0 20px;
    width: 220px;
    .name_zh {
      font-size: 12px;
      line-height: 15px;
    }

    .name_en {
      font-size: 10px;
      line-height: 15px;
    }
  }
  .thumbnail {
    height: 40px;
    width: 40px;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .variations {
    margin: 0px;
    padding: 0px;
    height: 100%;
    li {
      padding: 0;
      margin: 0;
      margin-left: 3px;
      border-radius: 3px;
      list-style: none;
      margin-right: 10px;
      display: inline-block;
      height: 100%;
      width: 40px;
      vertical-align: top;
    }
  }
}

.variation {
  transition: all 0.1s ease;
  cursor: pointer;
  width: 50px;
  text-align: center;
  height: 100%;
  position: relative;
  opacity: 0.5;

  .variation__thumbnail {
    width: 25px;
    height: 25px;
    img {
      height: 100%;
      width: 100%;
    }
  }
  .variation__text {
    position: absolute;
    bottom: 0;
    font-size: 10px;
    text-align: center;
    right: 0;
    left: 0;
  }
  &:hover {
    transform-origin: center;
    transform: scale(1.1);
    background-color: #ccc;
  }
  &.variation--isOwnd {
    background-color: #40474e;
    color: #fff;
    opacity: 1;
  }
}
</style>
