<template>
  <div class="books main" ref="addImage">
    <div style="    margin: 20px 45px;">
      <el-button @click="save()">Save</el-button>
      <el-button @click="generatorImage">download image (beta)</el-button>
    </div>

    <div ref="capture">
      <ul>
        <li v-for="(item, index) in listDatas" :key="index" style="list-style:none">
          <card :item="item" :index="index" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import card from "./card.vue";
import datas from "@/output.js";
import html2canvas from "html2canvas";
// const datas = []

export default {
  name: "Books",
  components: {
    card
  },
  data() {
    return {
      datas
    };
  },
  computed: {
    bookFile: {
      get() {
        return this.$store.state.bookFile;
      },
      set(value) {
        this.$store.commit("UPDATE_BOOKFILE", value);
      }
    },
    listDatas() {
      return this.$store.state.listDatas;
    }
  },
  mounted() {
    console.log(datas.obj);
    this.$store.commit("LOAD_BOOKFILE");
  },
  methods: {
    save() {
      this.$store.commit("SAVE_BOOKFILE", JSON.stringify(this.bookFile));
    },
    generatorImage() {
      html2canvas(this.$refs.capture, { useCORS: true }).then(canvas => {
        this.$refs.addImage.append(canvas);
        // a
        let link = document.createElement("a");
        link.href = canvas.toDataURL();
        link.setAttribute("download", "canvas.png");
        link.style.display = "none";
        document.body.appendChild(link);
        link.click();
      });
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped >
.main {
  background-color: #f3f8ff;
  padding: 20px 0;
}

.books {
  ul {
    margin: 0;
  }
}
</style>
